import switchc from '@/components/zz-switchc/zz-switchc.vue'

export default {
	components: {
		switchc
	},
	data() {
		return {
			temp: [],
			list: ["全部", "理学", "农业", "法学", "工学", "哲学", "文学", "艺术学", "管理学"],
			switcha: true,

		};
	},
	onLoad() {
		var me = this;
		uni.showLoading({
			mask: true,
			title: "请稍后..."
		});

		uni.request({
			url: "http://nanhai655.cn:8080/user/xiangqing",
			methods: "POST",
			success: (res) => {

				var trailerList = res.data.list;
				me.trailerList = trailerList;
			},
			complete: () => {
				uni.hideNavigationBarLoading();
				uni.hideLoading();
				uni.stopPullDownRefresh();
			}
		})


		uni.request({
			url: "http://134.175.204.38:66/tuishu/getTuiShu",
			method: "GET",
			success(res) {
				me.temp = [];
				var temp = res.data.data;
				me.temp = temp;
				console.log(temp);
			}
		})

	},
	methods: {
		dianji(number) {
			console.log(number);
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
			console.log(this.modalName)
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},

		switchchange(e) {
			var me = this;
			console.log(e.value);
			me.switcha = e.value;

		},

		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}
}
